/*++++++++++++++++ FICHE LOGEMENT */
div.carrousel {
  position: relative;
  width: 100%;
  height: 415px;
  border-radius: 25px;
  overflow: hidden;
}
div.carrousel figure {
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: baseline;
      -ms-flex-align: baseline;
          align-items: baseline;
  position: relative;
}
div.carrousel figure img, div.carrousel figure img.x-plan {
  position: absolute;
  z-index: 0;
  width: 100%;
  height: inherit;
  -o-object-fit: cover;
     object-fit: cover;
}
div.carrousel figure img.first-plan {
  z-index: 2;
}
div.carrousel figure img.second-plan {
  z-index: 1;
}
div.carrousel figure figcaption {
  position: absolute;
  z-index: 3;
  bottom: 0;
  color: var(--color-secondary);
}
div.carrousel span.left-click,
div.carrousel span.right-click {
  position: absolute;
  cursor: pointer;
  z-index: 3;
  width: var(--carrousel-arrow-w);
  height: var(--carrousel-arrow-h);
  top: calc(50% - (var(--carrousel-arrow-h) + var(--carrousel-arrow-border-w) * 2) / 2);
}
div.carrousel span.right-click::before,
div.carrousel span.left-click::after {
  content: "";
  position: relative;
  display: inline-block;
  width: 100%;
  height: 100%;
  border-radius: 3px;
  border-bottom: var(--carrousel-arrow-border-w) solid var(--color-secondary);
  border-right: var(--carrousel-arrow-border-w) solid var(--color-secondary);
}
div.carrousel span.right-click {
  right: 15px;
}
div.carrousel span.right-click::before {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
div.carrousel span.left-click {
  left: 15px;
}
div.carrousel span.left-click::after {
  -webkit-transform: rotate(135deg);
          transform: rotate(135deg);
}
div.carrousel .one-pic {
  display: none;
}

